import { getDate, getTime } from '../utils/index'
export default {
  name: 'Clock',
  props: {},
  data() {
    return {
      time: '',
      date: ''
    }
  },
  created() {
    setInterval(this.updateTime, 1000)
  },
  mounted() {},
  methods: {
    updateTime() {
      const now = new Date()
      this.time = getTime(now)
      this.date = getDate(now)
    }
  }
}
